@import url(https://fonts.googleapis.com/css?family=Walter+Turncoat&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  font-family: "Walter Turncoat", cursive; }

body {
  padding-left: 40px;
  background: url(/15-puzzle_game-react/static/media/background.a05f1fb3.png); }

.main-title {
  margin: 20px 0;
  color: #5f6aff; }

.game {
  display: flex;
  align-items: center;
  flex-direction: column; }
  .game__grid {
    width: 440px;
    height: 440px;
    padding: 20px;
    display: flex;
    flex-wrap: wrap;
    background-color: rgba(0, 119, 24, 0.29);
    border-radius: 10px; }
  .game__tile {
    width: 90px;
    height: 90px;
    margin: 5px;
    border: none;
    background-color: #ffffa5;
    border-radius: 10px;
    font-size: 36px;
    font-family: "Walter Turncoat", cursive;
    font-weight: bolder;
    text-align: center;
    line-height: 100px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
    outline: none; }
    .game__tile:hover {
      background-color: rgba(255, 255, 165, 0.88); }
    .game__tile--invisible {
      background-color: rgba(0, 0, 0, 0.11);
      cursor: unset; }
      .game__tile--invisible:hover {
        background-color: rgba(0, 0, 0, 0.11); }
  .game__message {
    height: 50px;
    line-height: 50px; }
  .game__new-game-btn {
    outline: none;
    cursor: pointer;
    font-size: 24px;
    font-family: "Walter Turncoat", cursive;
    border-color: #5f6aff;
    padding: 0 10px;
    -webkit-transition: color 1s, background-color 1s;
    transition: color 1s, background-color 1s; }
    .game__new-game-btn:hover {
      background-color: #5f6aff;
      color: white; }

